import { defineComponent, computed, watch, onUnmounted } from 'vue';
import { useStore } from 'vuex';
import { NButton, useDialog, useMessage } from 'naive-ui';
import ConsoleColorPicker from '../components/ConsoleColorPicker.vue';
import ConsoleInput from '../components/ConsoleInput.vue';
export default defineComponent({
    name: 'PreviewConsole',
    components: {
        NButton,
        ConsoleColorPicker,
        ConsoleInput
    },
    setup() {
        const $store = useStore();
        const dialog = useDialog();
        const message = useMessage();
        const componentStyle = computed(() => $store.state.componentConsoleStyle);
        const menuActive = computed(() => $store.state.menuActive);
        const revert = () => {
            message.info('撤销，还没做呢');
        };
        const reset = () => {
            dialog.error({
                title: '我是一个提醒',
                content: '要重置全部已更改的样式嘛？没有后悔药哦~',
                positiveText: '是的',
                negativeText: '不了',
                maskClosable: false,
                onPositiveClick: () => {
                    $store.dispatch('resetTheme');
                    message.success('好了，现在我变回原来的我了');
                }
            });
        };
        const download = async () => {
            const currentThemeInfo = await $store.dispatch('getCurrentThemeInfo');
            $store.dispatch('downloadTheme', currentThemeInfo);
            message.success('下载好了');
        };
        const stopWatchMenuActive = watch(menuActive, newState => {
            $store.dispatch('getComponentConsoleStyle', newState);
        }, { immediate: true });
        onUnmounted(() => {
            stopWatchMenuActive();
        });
        return {
            componentStyle,
            revert,
            reset,
            download
        };
    }
});
