export default defineComponent({
    name: 'PagePreview',
    components: {
        NLayout,
        NLayoutSider,
        PreviewMenu,
        PreviewContent,
        previewConsole
    },
    setup() {
        const $store = useStore();
        $store.dispatch('getThemePreviewConfig');
        $store.dispatch('menuChange', 'Base');
    }
});
